.phases{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--secondary);
    position: relative;
    padding-top: 10px;
    border-radius: 5px;
}
.phasesButtons{
    width: 100%;
    display: flex;
    height: 40px;
    margin-top: auto;
    border-top: 1px solid var(--general-border);
    align-items: center;
}
.phaseButton.selectedButton{
    background-color: white;
    color: black;
}
.phaseContent{
    height: 250px;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 0 1rem;
}
.addPhaseButton{
    background-color: transparent;
    border: none;
    width: 50px;
    flex-shrink: 0;
    
}
.phaseButton{
    background-color: transparent;
    border: none;
    height: 40px;
    padding: 0 10px;
    white-space: nowrap;
}
.addExerciseButton{
    background-color: var(--background);
    border-radius: 5px;
    border: none;
    height: 40px;
    width: 100%;
    flex-shrink: 0;
}
.phasesHeader{
    height: 40px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
}
.phasesHeader button{
    background-color: transparent;
    border: none;
}
.phaseExercise{
    width: 100%;
    height: 40px;
    background-color: var(--background);
    display: flex;
    align-items: center;
    border-radius: 5px;
    padding: 0 10px;
    gap: 10px;
    flex-shrink: 0;
}
.phaseExercises{
    display: flex;
    flex-direction: column;
    gap: 10px;
    overflow-y: auto;
    padding-bottom: 25px;
}
.phaseExercise b{
    margin-right: auto;

}
.phaseExercise button{
    background-color: transparent;
    border: none;
}
.phaseExercise button img{
    width: 25px;
    height: 25px;
}